@import 'reset';
@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Fredericka+the+Great&family=Henny+Penny&display=swap');

figure {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;

  h1 {
    font: normal italic 60px/1 'Fredericka the Great';
    color: #222;
    position: absolute;
    top: 5vh;
    left: 7vw;
    z-index: 2;
  }

  .cameraBtns {
    position: absolute;
    top: 7vh;
    right: 10vw;
    display: flex;
    z-index: 5;
    gap: 60px;
    
    li {
      font: italic 20px/1 'Architects Daughter';
      color: #222;
      cursor: pointer;
      transition: 0.5s;

      &.on {
        transform: scale(1.3);
        color: #000;
        font-weight: bold;
      }
    }    
  }

  .txt {
		article {
			position: absolute;
			right: 10vw;
			top: 30vh;
			z-index: -1;
			opacity: 1;
			transition: 0.5s;

			h2 {
				font: normal italic 9vw/1 'Henny Penny';
				color: #444;
				span {
          display: inline-block;
          opacity: 0;
          transform: scale(3);
          transition-duration: 0.5s;
          transition-timing-function: cubic-bezier(0.48, -0.69, 0.53, 1.59);
        }
			}

      p {
        font: 14px/1.4 'arial';
        color: #555;
        position: absolute;
        z-index: 2;
        transition: 1s 1s;
        opacity: 0;
        transform: translateX(100px);
      }

			&.on {
        z-index: 2;
				h2 {
          span {
            opacity: 1;
            transform: scale(1);
          }
        }
        p {
          opacity: 1; 
          transform: translateX(0px);
        }
			}

			&:nth-of-type(1) {
				right: 10vw;
				top: 30vh;
        p {
          width: 26vw;
          margin-top: 20px;
          margin-left: 100px;
        }
			}
			&:nth-of-type(2) {
				right: 10vw;
				top: 30vh;
        p {
					width: 18vw;
					margin-top: 50px;
					margin-left: 20px;
				}
			}
			&:nth-of-type(3) {
				left: 10vw;
				bottom: 15vh;
        p {
					width: 26vw;
					margin-top: 20px;
					margin-left: 0px;
				}
			}
			&:nth-of-type(4) {
				left: 10vw;
				bottom: 15vh;
        p {
					width: 36vw;
					margin-top: 12px;
					margin-left: 0px;
				}
			}
			&:nth-of-type(5) {
				left: 10vw;
				bottom: 15vh;
        p {
					width: 20vw;
					margin-top: 50px;
					margin-left: -10px;
				}
			}
			&:nth-of-type(6) {
				right: 5vw;
				top: 30vh;
        p {
					margin-top: 50px;
					margin-left: 30px;
				}
			}
		}
  }

  .colorPicker {
    position: absolute;
    bottom: 10vh;
    right: 10vw;
    z-index: 2;
    display: flex;

    li {
      width: 100px;
      height: 10px;
      border: 1px solid #222;
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 1399px){
  figure {
    h1 {font-size: 40px; left: 5vw;}
    .cameraBtns {
      right: 5vw;
      gap: 40px;
      li {font-size: 18px;}
    }
  }  
}
@media screen and (max-width: 899px){
  figure {
    h1 {font-size: 30px; top: 3vh;}
    .cameraBtns {
      top: 11vh;
      right: auto;
      left: 5vw;
      gap: 20px;  
      li {font-size: 16px;}
    }
  }  
}